// =============================================================================
// SMF Probe Data API
// =============================================================================
// GET /api/v1/smf/probes
// Returns probe attempt data and statistics

import { NextResponse } from 'next/server';
import { getDb } from '@/db/client';
import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'SMFProbesAPI' });

export const dynamic = 'force-dynamic';

export async function GET(request: Request) {
  try {
    const { searchParams } = new URL(request.url);
    const limit = parseInt(searchParams.get('limit') || '100', 10);
    const offset = parseInt(searchParams.get('offset') || '0', 10);
    const facilitatorId = searchParams.get('facilitator_id');
    const network = searchParams.get('network');
    const token = searchParams.get('token');
    const resultFilter = searchParams.get('result');
    const hours = parseInt(searchParams.get('hours') || '24', 10);

    const db = getDb();
    const isPostgres = 'pool' in db || typeof (db as any).pool?.query === 'function';

    if (isPostgres) {
      // Get total probe attempts
      const totalResult = await (db as any).pool.query(
        `SELECT COUNT(*) as count FROM route_attempts WHERE is_probe = true`
      );
      const totalProbes = parseInt(totalResult.rows[0].count);

      // Get detailed probe attempts with route information
      let recentQuery = `
        SELECT 
          ra.id,
          ra.route_id,
          ra.facilitator_id,
          ra.phase,
          ra.result,
          ra.latency_ms,
          ra.error_code,
          ra.raw_status,
          ra.is_probe,
          ra.created_at,
          r.network,
          r.token,
          r.amount,
          r.status as route_status,
          r.request_id,
          r.correlation_id,
          r.client_id,
          r.agent_id
        FROM route_attempts ra
        LEFT JOIN routes r ON ra.route_id = r.id
        WHERE ra.is_probe = true
      `;
      
      const queryParams: (string | number)[] = [];
      let paramIndex = 1;

      // Add time filter
      if (hours > 0) {
        recentQuery += ` AND ra.created_at > NOW() - INTERVAL '${hours} hours'`;
      }

      // Add filters
      if (facilitatorId) {
        recentQuery += ` AND ra.facilitator_id = $${paramIndex++}`;
        queryParams.push(facilitatorId);
      }
      if (network) {
        recentQuery += ` AND r.network = $${paramIndex++}`;
        queryParams.push(network);
      }
      if (token) {
        recentQuery += ` AND r.token = $${paramIndex++}`;
        queryParams.push(token);
      }
      if (resultFilter) {
        recentQuery += ` AND ra.result = $${paramIndex++}`;
        queryParams.push(resultFilter);
      }

      recentQuery += ` ORDER BY ra.created_at DESC LIMIT $${paramIndex++} OFFSET $${paramIndex++}`;
      queryParams.push(limit, offset);

      const recentResult = await (db as any).pool.query(recentQuery, queryParams);

      // Get probe attempts by facilitator
      const byFacilitatorResult = await (db as any).pool.query(
        `SELECT 
          facilitator_id,
          COUNT(*) as total,
          SUM(CASE WHEN result = 'success' THEN 1 ELSE 0 END) as successes,
          SUM(CASE WHEN result != 'success' THEN 1 ELSE 0 END) as failures,
          AVG(latency_ms)::integer as avg_latency_ms,
          MIN(created_at) as first_probe,
          MAX(created_at) as last_probe
        FROM route_attempts 
        WHERE is_probe = true 
        GROUP BY facilitator_id`
      );

      // Get probe attempts by result
      const byResultResult = await (db as any).pool.query(
        `SELECT 
          result,
          COUNT(*) as count
        FROM route_attempts 
        WHERE is_probe = true 
        GROUP BY result
        ORDER BY count DESC`
      );

      // Get probe attempts in last hour
      const lastHourResult = await (db as any).pool.query(
        `SELECT COUNT(*) as count 
        FROM route_attempts 
        WHERE is_probe = true 
        AND created_at > NOW() - INTERVAL '1 hour'`
      );
      const probesLastHour = parseInt(lastHourResult.rows[0].count);

      // Get count with same filters for pagination
      let countQuery = `
        SELECT COUNT(*) as count
        FROM route_attempts ra
        LEFT JOIN routes r ON ra.route_id = r.id
        WHERE ra.is_probe = true
      `;
      const countParams: (string | number)[] = [];
      let countParamIndex = 1;

      if (hours > 0) {
        countQuery += ` AND ra.created_at > NOW() - INTERVAL '${hours} hours'`;
      }
      if (facilitatorId) {
        countQuery += ` AND ra.facilitator_id = $${countParamIndex++}`;
        countParams.push(facilitatorId);
      }
      if (network) {
        countQuery += ` AND r.network = $${countParamIndex++}`;
        countParams.push(network);
      }
      if (token) {
        countQuery += ` AND r.token = $${countParamIndex++}`;
        countParams.push(token);
      }
      if (resultFilter) {
        countQuery += ` AND ra.result = $${countParamIndex++}`;
        countParams.push(resultFilter);
      }

      const countResult = await (db as any).pool.query(countQuery, countParams);
      const filteredCount = parseInt(countResult.rows[0].count);

      return NextResponse.json({
        summary: {
          total: totalProbes,
          lastHour: probesLastHour,
          filtered: filteredCount,
          showing: recentResult.rows.length,
          limit,
          offset,
        },
        filters: {
          facilitatorId: facilitatorId || null,
          network: network || null,
          token: token || null,
          result: resultFilter || null,
          hours,
        },
        byFacilitator: byFacilitatorResult.rows.map((row: any) => ({
          facilitatorId: row.facilitator_id,
          total: parseInt(row.total),
          successes: parseInt(row.successes),
          failures: parseInt(row.failures),
          successRate: row.total > 0 ? ((row.successes / row.total) * 100).toFixed(1) + '%' : '0%',
          avgLatencyMs: row.avg_latency_ms,
          firstProbe: row.first_probe,
          lastProbe: row.last_probe,
        })),
        byResult: byResultResult.rows.map((row: any) => ({
          result: row.result,
          count: parseInt(row.count),
        })),
        recentAttempts: recentResult.rows.map((row: any) => ({
          id: row.id,
          routeId: row.route_id,
          facilitatorId: row.facilitator_id,
          network: row.network,
          token: row.token,
          amount: row.amount,
          phase: row.phase,
          result: row.result,
          latencyMs: row.latency_ms,
          errorCode: row.error_code,
          rawStatus: row.raw_status,
          isProbe: row.is_probe,
          routeStatus: row.route_status,
          requestId: row.request_id,
          correlationId: row.correlation_id,
          clientId: row.client_id,
          agentId: row.agent_id,
          timestamp: row.created_at,
          createdAt: row.created_at,
        })),
        timestamp: new Date().toISOString(),
      });
    } else {
      // SQLite
      const totalStmt = (db as any).db.prepare(
        `SELECT COUNT(*) as count FROM route_attempts WHERE is_probe = 1`
      );
      const totalProbes = totalStmt.get().count;

      const recentStmt = (db as any).db.prepare(
        `SELECT 
          id, 
          facilitator_id, 
          phase, 
          result, 
          latency_ms, 
          error_code, 
          raw_status,
          created_at
        FROM route_attempts 
        WHERE is_probe = 1 
        ORDER BY created_at DESC 
        LIMIT 20`
      );
      const recentProbes = recentStmt.all();

      return NextResponse.json({
        summary: {
          total: totalProbes,
          lastHour: 'N/A (SQLite)',
        },
        recentAttempts: recentProbes.map((row: any) => ({
          id: row.id,
          facilitatorId: row.facilitator_id,
          phase: row.phase,
          result: row.result,
          latencyMs: row.latency_ms,
          errorCode: row.error_code,
          rawStatus: row.raw_status,
          timestamp: row.created_at,
        })),
        timestamp: new Date().toISOString(),
      });
    }
  } catch (error) {
    logger.error({ error }, 'Failed to get probe data');
    return NextResponse.json(
      {
        error: error instanceof Error ? error.message : 'Unknown error',
        timestamp: new Date().toISOString(),
      },
      { status: 500 }
    );
  }
}

